import React from "react";
import classnames from "classnames";
import Select, {Option} from "rc-select";
import "app/MentalHealthcomponents/CustomComponent/Select/index.less";
import Utilies from "Utilies/Utilies";


class SelectComponent extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            value: "",
            data: []
        };
    }


    componentDidMount() {
        if (this.props.data && this.props.data.length > 0) {
            this.setValue(this.props.value, this.props.data);
        }
    }


    componentWillReceiveProps(nextProps) {
        if (nextProps.data && nextProps.data.length > 0) {
            this.setValue(nextProps.value, nextProps.data);
        }
    }


    setValue(value, data) {
        const name = data.filter((item) => {
            if (Utilies.isNull(item.id)) {
                return item === value;
            } else {
                if (item.id === value) {
                    return item;
                }
            }
            return null;
        });

        if (name.length > 0) {
            if (Utilies.isNull(name[0].name)) {
                this.setState({
                    value: name[0]
                });
            } else {
                this.setState({
                    value: name[0].name
                });
            }
        }
    }


    onChangeAction(value, data) {
        const {datakey} = this.props;
        if (datakey) {
            this.props.onChange(value, datakey);
        } else {
            this.props.onChange(value, data["props"]["title"]);
        }
    }


    render() {
        const {
            name,
            data,
            // needEmpty,
            // value,
            style,
            position,
            title,
            disabled,
            nameIcon,
            descDetail,
            descName,
            placeholder,
            // selected,
            innerboxstyle,
            allowClear,
            showWarn
        } = this.props;
        const classes = classnames({
            "bi-input": true,
            "bi-input-position-span": position === "left"
        });

        const options = data.map((item) => {
            if (Utilies.isNull(item.id)) {
                return <Option key={item} title={item} value={item}>{item}</Option>;
            } else {
                return <Option key={item.id} title={item.name} value={item.id}>{item.name}</Option>;
            }
        });

        return (
            <div className={classes} style={style}>
                {
                    name ?
                        (
                            name.indexOf("*") === -1 ?
                                <span style={this.props.titleStyle} className={"input-span"}>{nameIcon ?
                                    <i className={"showTitle iconfont " + nameIcon}
                                       style={{
                                           color: "#1890FF",
                                           position: "relative",
                                           bottom: "-1px",
                                           fontSize: "14px",
                                           marginRight: "3px"
                                       }}>
                                        <span><p>{descName}:</p>{descDetail}</span>
                                    </i> : ""}{name}</span> :
                                <span style={this.props.titleStyle}
                                      className={"input-span"}>{nameIcon ?
                                    <i className={"showTitle iconfont " + nameIcon}
                                       style={{
                                           color: "#1890FF",
                                           position: "relative",
                                           bottom: "-1px",
                                           fontSize: "14px",
                                           marginRight: "3px"
                                       }}>
                                        <span><p>{descName}:</p>{descDetail}</span>
                                    </i> : ""}{name.replace("*", "")}<span
                                    style={{color: "red"}}>*</span></span>)
                        : ""
                }
                <Select style={innerboxstyle} className={"select-name"}
                        dropdownClassName={"dropdownClassName"}
                        placeholder={placeholder}
                        showSearch={false}
                        disabled={disabled}
                        allowClear={allowClear}
                        value={this.state.value}
                        dropdownMenuStyle={{maxHeight: "200px", overflowX: "hidden", overflowY: "auto"}}
                        onChange={this.onChangeAction.bind(this)}>
                    {options}
                </Select>
                {
                    showWarn ? <i className={"showTitle iconfont icon-jinggao red"}
                                  style={{
                                      position: "absolute",
                                      bottom: "5px",
                                      fontSize: "14px",
                                      right: "8px",
                                      background: "#fff"
                                  }}><span style={{right: "-8px", left: "auto"}}>{title}</span></i> : ""
                }
            </div>

        );
    }
}


export default SelectComponent;



