import React from "react";
import "app/MentalHealthcomponents/CustomComponent/Button/index.less";
import classnames from 'classnames';


class Button extends React.Component {

    onClick(e) {
        this.props.onClick && this.props.onClick(e);
    }


    render() {
        const {code, type, disabled} = this.props;
        const classes = classnames({
            'bi-button': true,
            "cancle": type === 'cancle'
        });

        return (
            <button disabled={disabled} data-id={code} onClick={this.onClick.bind(this)} className={classes} style={this.props.style}>
                <span>{this.props.children}</span>
            </button>
        );
    }
}

export default Button;



