import React from "react";
import "app/MentalHealthcomponents/CustomComponent/Alert/index.less";
import Button from "app/MentalHealthcomponents/CustomComponent/Button/Button";
import Input from 'app/MentalHealthcomponents/CustomComponent/Input/Input';
import Select from "app/MentalHealthcomponents/CustomComponent/Select/Select";


class Alert extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            visible: false,
            inputValue: "",
            inputShow: false,
            selectValue: props.defaultOptions
        };
    }


    componentDidMount() {
        this.setState({
            visible: true
        });
    }


    clickAction(e) {
        const {promise, modal, showInput, useSureBtn} = this.props;

        if (modal === 'prompt') {
            if (showInput) {
                if (this.state.inputValue === '') {
                    if (e.currentTarget.getAttribute('data-id')) {
                        this.setState({
                            inputShow: true
                        });
                        return;
                    } else {
                    }
                } else {
                    if (e.currentTarget.getAttribute("data-id")) {
                        promise.resolve(this.state.inputValue);
                    } else {
                        // promise.resolve();
                    }
                }
            } else {
                promise.resolve(e);
            }
        } else if (useSureBtn) {
            promise.resolve(e);
        } else if (modal === 'selectModel') {
            if (e.currentTarget.getAttribute("data-id")) {
                promise.resolve(this.state.selectValue);
            }
        } else {
            if (e.currentTarget.getAttribute("data-id")) {
                promise.resolve(e.currentTarget.getAttribute("data-id"));
            }
        }

        this.close();
    }


    onSelectValueChange(value) {
        this.setState({
            selectValue: value
        });
    }


    close() {
        const {willUnmount} = this.props;
        document.body.style.setProperty('overflow', 'auto');
        this.setState({
            visible: false
        }, function () {
            willUnmount();
        });
    }


    onChange(value) {
        this.setState({
            inputValue: value
        });
    }


    render() {
        const {
            title, message,
            type, code, inputPlaceholder,
            showInput, showSelect, options
        } = this.props;

        let useCode = code;
        if (showInput || showSelect) {
            useCode = true;
        }

        return (
            <div className={this.state.visible ? "alert-container" : "alert-container hidden"}>
                <div>
                    <div className={"alert-inner"}>
                        <div className={"alert-wrapper"}>
                            <div className={"alert-box"}>
                                {
                                    title && (
                                        <div className={`alert-title alert-${type}-title`}>
                                            {title}
                                        </div>
                                    )
                                }


                                {
                                    message && (
                                        <div className={'alert-message'}>
                                            {message}
                                        </div>
                                    )
                                }

                                <div className={this.state.inputShow ? "tips show" : "tips"}>{inputPlaceholder}</div>

                                {
                                    showInput && (
                                        <div>
                                            <Input position={'left'} name={"名称"}
                                                   style={this.state.inputShow ? {} : {marginTop: "10px"}}
                                                   value={this.state.inputValue} key={'message'}
                                                   onChange={this.onChange.bind(this)}
                                                   placeholder={inputPlaceholder}/>
                                        </div>
                                    )
                                }

                                {
                                    showSelect && (
                                        <div style={{marginTop: "10px"}}>
                                            <Select position={"left"}
                                                    needEmpty={false}
                                                    value={this.state.selectValue}
                                                    name={"位置"}
                                                    data={options}
                                                    onChange={this.onSelectValueChange.bind(this)}></Select>
                                        </div>
                                    )
                                }

                                <div className={'alert-footer'}>
                                    {
                                        (type === "success" || type === 'error') ? null
                                            :
                                            <Button onClick={this.clickAction.bind(this)}>取消</Button>
                                    }

                                    <Button code={useCode} onClick={this.clickAction.bind(this)}>确定</Button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default Alert;



