import React from "react";
import "app/MentalHealthcomponents/CustomComponent/Input/index.less";


class Input extends React.Component {

    handleChange(e) {
        e.stopPropagation();
        const {onChange, datakey} = this.props;

        const {parseValue} = this.props;
        if (e.target.value === '') {
            if (onChange) {
                if (datakey) {
                    onChange(e.target.value, datakey);
                } else {
                    onChange(e.target.value);
                }
            }
        } else {
            if (parseValue) {
                let patt = RegExp(parseValue, 'g');        // 这里转换正则对象
                if (patt.test(e.target.value)) {
                } else {
                    if (onChange) {
                        if (datakey) {
                            onChange(e.target.value, datakey);
                        } else {
                            onChange(e.target.value);
                        }
                    }
                }
            } else {
                if (onChange) {
                    if (datakey) {
                        onChange(e.target.value, datakey);
                    } else {
                        onChange(e.target.value);
                    }
                }
            }
        }
    }


    handleFocus(e) {
        e.stopPropagation();
        const {onFocus, datakey} = this.props;
        if (onFocus) {
            if (datakey) {
                onFocus(e.target.value, datakey);
            } else {
                onFocus(e.target.value);
            }
        }
    }


    handleBlur(e) {
        e.stopPropagation();
        const {onBlur, datakey} = this.props;
        if (onBlur) {
            if (datakey) {
                onBlur(e.target.value, datakey);
            } else {
                onBlur(e.target.value);
            }
        }
    }


    className(position) {
        if (position) {
            if (position === 'left') {
                return "bi-input bi-input-position-span";
            } else if (position === 'right') {

            } else if (position === 'bottom') {

            } else {
                return "bi-input";
            }
        }
        return "bi-input";
    }


    onClickAction(e) {
        this.props.searchAction();
    }


    render() {
        const {
            name,
            position,
            placeholder,
            icon,
            nameIcon,
            descDetail,
            descName,
            inputStyle,
            value,
            type,
            need,
            disabled,
            // hidden,
            title,
            seacrhIcon,
            style,
            showWarn,
            maxlength,
            iconStyle,
            titleStyle
        } = this.props;

        let usetype = type || "input";

        return (
            <div className={this.className(position)} style={style} onClick={(e) => {
                e.stopPropagation();
            }}>
                {name && position && (
                    <span className={"input-span"}>
                          <span className={"input_title"} style={titleStyle}>{name}</span>
                        {need && (
                            <span style={{color: "red"}}>*</span>)
                        }
                        {nameIcon ? <i className={"showTitle iconfont " + nameIcon}
                                       style={{
                                           color: "#1890FF",
                                           position: "relative",
                                           bottom: "-1px",
                                           fontSize: "14px",
                                           marginLeft: "5px"
                                       }}><span><p>{descName}:</p>{descDetail}</span></i> : ""}


                    </span>
                )
                }

                {
                    name && !position && (
                        <span className={"input-span"}>
                            {nameIcon ? <i className={"showTitle iconfont " + nameIcon}
                                           style={{
                                               color: "#1890FF",
                                               position: "relative",
                                               bottom: "-1px",
                                               fontSize: "14px",
                                               marginRight: "3px"
                                           }}><span><p>{descName}:</p>{descDetail}</span></i> : ""}
                            {name}
                            {need && (
                                <span style={{color: "red"}}>*</span>)
                            }
                    </span>
                    )
                }

                <div className={"positionFlex"} style={name && !position ? {"marginTop": "10px"} : {"marginTop": "0"}}>
                    {
                        icon ? <i className={seacrhIcon ? "input-icon seacrh-icon" : "input-icon"}
                                  style={iconStyle}
                                  onClick={this.onClickAction.bind(this)}><img src={icon} alt=""/></i> : ''
                    }
                    <input
                        type={usetype}
                        disabled={disabled}
                        value={value}
                        style={inputStyle}
                        placeholder={placeholder}
                        maxLength={maxlength ? maxlength : null}
                        onChange={this.handleChange.bind(this)}
                        onFocus={this.handleFocus.bind(this)}
                        onBlur={this.handleBlur.bind(this)}/>
                    {
                        showWarn ? <i className={"showTitle iconfont icon-jinggao red"}
                                      style={{
                                          position: "absolute",
                                          bottom: "5px",
                                          fontSize: "14px",
                                          right: "8px",
                                          background: "#fff"
                                      }}><span style={{right: "-8px", left: "auto"}}>{title}</span></i> : ''
                    }
                </div>
            </div>
        );
    }
}

export default Input;



