import React from 'react';
import ReactDOM from 'react-dom';
import Alert from 'app/MentalHealthcomponents/CustomComponent/Alert/Alert';


function msgbox(props) {
    return next(props);
}

function warningAlert(title, message, code, props) {
    return alert(title, message, code, 'warning', props);
}

function errorAlert(title, message, props) {
    return alert(title, message, '', 'error', props);
}

function successAlert(title, message, props) {
    return alert(title, message, '', 'success', props);
}

function alert(title, message, code, type, props) {
    if (typeof title === 'object') {
        props = title;
    }

    props = Object.assign({
        title, message, code, type,
        modal: 'alert',
        closeOnPressEscape: false,
        closeOnClickModal: false
    }, props);

    return next(props);
}

function prompt(title, message, props) {
    if (typeof title === 'object') {
        props = title;
    }
    const type = 'prompt';

    props = Object.assign({
        title, message, type,
        modal: 'prompt',
        showInput: true
    }, props);

    return next(props);
}

function selectAlert(title, props) {
    if (typeof title === 'object') {
        props = title;
    }
    const type = 'selectModel';

    props = Object.assign({
        title, type,
        modal: 'selectModel',
        showSelect: true
    }, props);

    return next(props);
}

function next(props) {
    return new Promise((resolve, reject) => {
        const div = document.createElement('div');
        div.setAttribute("class", "loadingClass");
        document.body.appendChild(div);

        if (props.lockScroll !== false) {
            document.body.style.setProperty('overflow', 'hidden');
        }

        const component = React.createElement(Alert, Object.assign({}, props, {
            promise: {resolve, reject},
            willUnmount: () => {
                ReactDOM.unmountComponentAtNode(div);
                document.body.removeChild(div);
                document.body.style.removeProperty('overflow');
            }
        }));

        ReactDOM.render(component, div);
    });
}


export default {
    msgbox,
    warningAlert,
    successAlert,
    errorAlert,
    prompt,
    selectAlert
};



